$text_color_blue: #1eacc7;
$text_color_red: #ff5656;

@mixin btn_hover {
    &:hover {
        background-image: none;
        background-color: $text_color_blue; } }

* {
    font-family: 'Roboto', sans-serif; }
header {
    position: relative;
    nav {
        height: 50px;
        background-color: #212121;
        .menu {
            padding: 13px 70px;
            margin: 0;
            display: flex;
            justify-content: space-between;
            list-style-type: none;
            &_link {
                    color: #ffffff;
                    font-size: 15px;
                    font-weight: 300;
                    font-style: normal;
                    letter-spacing: normal;
                    text-align: left;
                    border-right: 2px dotted black;
                    padding-right: 28px;
                    text-decoration: none; } } }
    .subheader {
        position: absolute;
        width: 100%;
        height: 84px;
        background-color: rgba(0,0,0,0.6);
        &_btn {
            width: 172px;
            height: 46px;
            box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
            border: none;
            background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
            border-radius: 5px;
            text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
            color: #ffffff;
            font-size: 14px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: normal;
            text-align: center;
            text-transform: uppercase;
            @include btn_hover; }
        .col-md-4 {
            margin-top: 16px;
            .logo_link {
                max-width: 100%; } }
        .col-md-2 {
            margin-top: 23px;
            .everyday {
                color: #ffffff;
                font-size: 12px;
                font-weight: 300;
                font-style: normal;
                letter-spacing: normal;
                text-align: right;
                width: 156px; }
            .number {
                color: #ffffff;
                font-size: 22px;
                font-weight: 400;
                font-style: normal;
                letter-spacing: normal;
                text-decoration: none; } } } }





.promo {
    background: url('../img/fistpanel/2-layers.png') center center /cover no-repeat;
    min-height: 799px;
    padding: 150px 0 135px 0;
    &_header {
        color: #fff;
        font-weight: 700;
        font-size: 60px;
        text-align: center; }
    &_subheader {
        color: #ffffff;
        font-size: 36px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 48px;
        text-align: center; }
    &_text {
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: center;
        margin-top: 23px; }
    &_btn {
        display: block;
        margin: 118px auto 0 auto;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border: none;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        border-radius: 5px;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        text-transform: uppercase;
        @include btn_hover; } }
.job {
    min-height: 900px;
    padding: 71px 0 65px 0;
    .circle {
        width: 113px;
        height: 113px;
        background-color: #1eacc7;
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center; }
    .row {
        margin-top: 35px; }
    &_headertext {
        color: #1eacc7;
        font-size: 17px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 21px;
        text-align: justifyLeft;
        margin-left: -26px; }
    &_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        line-height: 21px;
        text-align: justifyLeft;
        margin-top: 5px;
        margin-left: -26px; } }
.black_box {
        margin: 0 auto;
        width: 112px;
        height: 24px;
        padding: 5px 12px;
        background-color: #000000;
        color: #ffffff;
        font-size: 11px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        text-transform: uppercase;
        &_white {
            background-color: #ffffff;
            color: #070716; } }
.title {
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 48px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 40px;
    &_white {
        color: #ffffff; } }
.subtitle {
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    text-align: center;
    margin-top: -30px;
    &_white {
        color: #ffffff; } }
.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/thirdpanel/Rectangle_20.png') center center /cover no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 91px;
        &_1 {
            background: url('../img/thirdpanel/2-layers.png'); }
        &_2 {
            background: url('../img/thirdpanel/3-layers.png'); }
        &_3 {
            background: url('../img/thirdpanel/4-layers.png'); }
        &_4 {
            background: url('../img/thirdpanel/5-layers.png'); }
        &_5 {
            background: url('../img/thirdpanel/6-layers.png'); }
        &_6 {
            background: url('../img/thirdpanel/7-layers.png'); } }
    &_subtitle {
        color: #ffffff;
        font-size: 24px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        text-transform: uppercase; }
    &_item_plus {
        width: 26px;
        height: 26px;
        background-color: #000000;
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 35px;
        left: 50%;
        transform: translateX(-50%);
        cursor: pointer;
        &::before {
            content: '';
            display: block;
            width: 14px;
            height: 2px;
            background-color: $text_color_blue;
            transition: 0.3s all; }

        &::after {
            content: '';
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            display: block;
            width: 2px;
            height: 14px;
            background-color: $text_color_blue;
            transition: 0.3s all; }


        &:hover {
            &::after, &::before {
                background-color: #fff; } } } }

.avto {
    position: relative;
    min-height: 896px;
    padding: 57px 0 138px 0;
    background: url('../img/fourthpanel/bg.png') center center /cover no-repeat;
    &_semicircle {
        display: block;
        margin: 0 auto;
        margin-top: 56px; }
    &_avtoimg {
        position: absolute;
        top: 385px;
        left: 50%;
        transform: translateX(-50%); }
    &_text {
        margin-top: 317px;
        color: #222222;
        font-size: 17px;
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: center; } }
.require {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nbm {
            margin-bottom: 0; } }
    &_title {
        font-size: 22px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        line-height: 24px;
        color: $text_color_blue;
        &_red {
            color: $text_color_red; } }
    &_list {
        margin-top: 15px;
        padding-left: 50px;


        li {
            position: relative;
            margin-bottom: 10px;
            font-size: 17px;
            font-weight: 300;
            font-style: normal;
            letter-spacing: normal;
            line-height: normal;
            list-style: none;
            &::before {
                content: '';
                display: block;
                position: absolute;
                left: -33px;
                width: 21px;
                height: 20px;
                background: url('../img/Layer_31_copy_6.png') center center /cover no-repeat; } }
        &_red {
            li {
                &::before {
                    background: url('../img/Layer_31_copy_9.png') center center /cover no-repeat; } } } }

    &_descr {
        color: $text_color_blue;
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }
.world {
    background: url('../img/fifthpanel/2-layers.png') center center /cover no-repeat;
    min-height: 717px;
    padding: 49px 0; }














